<template>
  <div>
    <Title />
    <el-header style="height:92px;">
      <el-col :span="12" style="margin-left:38%;">
        <div class="block">
          <el-avatar :size="80" :src="circleUrl"></el-avatar>
        </div>
        <!-- <div class="block" v-for="size in sizeList" :key="size">
            <el-avatar :size="size" :src="circleUrl"></el-avatar>
          </div> -->
      </el-col>
    </el-header>

    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-form-item label="客户名称" prop="cname">
        <el-input v-model="form.cname"></el-input>
      </el-form-item>
      <el-form-item label="客户星级" prop="starLevel">
        <el-input v-model="form.starLevel" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="投保意向">
        <el-select v-model="form.intention" placeholder="请选择">
          <el-option v-for="item in intention" :key="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <h4 style="padding-left: 3%;color:#409EFF;font-size:16px;">基本信息</h4>
      <el-form-item label="证件类型">
        <el-select v-model="form.codeType" placeholder="请选择">
          <el-option v-for="item in codeType" :key="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="行业类型">
        <el-select v-model="form.industryType" placeholder="请选择">
          <el-option v-for="item in industryType" :key="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="单位性质">
        <el-select v-model="form.unitType" placeholder="请选择">
          <el-option v-for="item in unitType" :key="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="证件号码">
        <el-input v-model="form.codeId"></el-input>
      </el-form-item>
      <el-form-item label="单位总人数" label-width="92px">
        <el-input v-model="form.unitCount" style="width:80%;"></el-input>
      </el-form-item>
      <el-form-item label="预计投保人数" label-width="102px">
        <el-input v-model="form.insureCount" style="width:80%;"></el-input>
      </el-form-item>
      <el-form-item label="地址" label-width="102px">
        <el-input v-model="form.address" style="width:80%;"></el-input>
      </el-form-item>
      <div v-for="(item, index) in form.linkmanList" :key="index">
        <h4 style="padding-left: 3%;" v-if="index == 0">
          <span style="color:#67C23A;">主联系人信息</span
          ><span style="margin-left:54%;color:#67C23A;" @click="addItem"
            >新增联系人</span
          >
        </h4>
        <h4 style="padding-left: 3%;" v-else-if="form.linkmanList.length >= 1">
          <span style="color:#E6A23C;">副联系人信息</span
          ><span
            style="margin-left:54%;color:#E6A23C;"
            @click="deleteItem(item, index)"
            >删除联系人</span
          >
        </h4>
        <el-form-item
          label="姓名"
          :prop="'linkmanList.' + index + '.lname'"
          :rules="[
            { required: true, message: '请输入姓名', trigger: 'blur' },
            {
              min: 2,
              max: 20,
              message: '长度在3到20个字符',
              trigger: ['blur', 'change']
            }
          ]"
        >
          <el-input v-model="item.lname"></el-input>
        </el-form-item>
        <el-form-item
          label="手机"
          :prop="'linkmanList.' + index + '.ltel'"
          :rules="[
            { required: true, message: '请输入手机号', trigger: 'blur' },
            { validator: checkTel, trigger: 'blur' }
          ]"
        >
          <el-input v-model="item.ltel"></el-input>
        </el-form-item>
        <el-form-item label="办公电话" prop="officePhone">
          <el-input v-model="item.officePhone"></el-input>
        </el-form-item>
        <el-form-item label="出生日期">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="item.hiredate"
              value-format="yyyy-MM-dd"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="部门" prop="dept">
          <el-input v-model="item.dept"></el-input>
        </el-form-item>
        <el-form-item label="职位" prop="job">
          <el-input v-model="item.job"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="item.email"></el-input>
        </el-form-item>
      </div>

      <el-form-item>
        <el-button type="success" style="width:50%;" @click="submitUser('form')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Title from "../nav/title.vue";
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
export default {
  name: "",
  components: {
    Title
  },
  data() {
    return {
      add: "",
      inten: "",
      code: "",
      industry: "",
      unit: "",
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      options: regionData,
      selectedOptions: [],
      form: {
        cname: "",
        codeId: "",
        unitCount: "",
        insureCount: "",
        industryType: null,
        codeType: null,
        intention: null,
        unitType: null,
        address: null,
        linkmanList: [
          {
            dept: "",
            email: "",
            flag: 1,
            hiredate: "",
            job: "",
            lname: "",
            ltel: "",
            officePhone: ""
          }
        ]
      },
      intention: ["意外险", "医疗险", "年金险", "重疾险", "其他"],
      codeType: [
        "营业执照",
        "组织机构代码证",
        "税务登记证",
        "社保保险登记证",
        "办学许可证",
        "其他"
      ],
      unitType: [
        "政府机关",
        "事业单位",
        "国有企业",
        "集体企业",
        "民营企业",
        "外资企业",
        "股份制企业",
        "合资企业",
        "其他"
      ],
      industryType: [
        "农业",
        "林业",
        "畜牧业",
        "渔业",
        "农、林、牧、渔服务业",
        "煤炭开采与洗选业",
        "石油和天然气开采业",
        "黑色金属矿采选业",
        "有色金属矿采选业",
        "非金属矿采选业",
        "其他矿采选业",
        "农副食品加工业",
        "食品制造业",
        "饮料制造业",
        "烟草制品业",
        "纺织业",
        "纺织服装、鞋、帽制造业",
        "皮革、毛皮、羽毛（绒）及其制品业",
        "木材加工及木、竹、藤、棕、草制品业",
        "家具制造业",
        "造纸及纸制品业",
        "印刷业、记录媒介的复制",
        "文教体育用品制造业",
        "石油加工、炼焦及核燃料加工业",
        "化学原料及化学制品制造业",
        "医药制造业",
        "化学纤维制造业",
        "橡胶制品业",
        "塑料制品业",
        "非金属矿物制品业",
        "黑色金属冶炼及压延加工业",
        "有色金属冶炼及压延加工业",
        "金属制品业",
        "通用设备制造业",
        "专业设备制造业",
        "交通运输设备制造业",
        "电气机械及器材制造业",
        "通信设备、计算机及其他电子设备制造业",
        "仪器仪表及文化、办公用机械制造业",
        "工艺品及其他制造业",
        "废弃资源和废旧材料回收加工业",
        "电力、热力的生产和供应业",
        "燃气生产和供应业",
        "水的生产和供应业",
        "房屋和土木工程建筑业",
        "建筑安装业",
        "建筑装饰业",
        "其他建筑业",
        "铁路运输业",
        "道路运输业",
        "城市公共交通业",
        "水上运输业",
        "航空运输业",
        "管道运输业",
        "装卸搬运和其他运输服务业",
        "仓储业",
        "邮政业",
        "电信和其他信息传输服务业",
        "计算机服务业",
        "软件业",
        "批发业",
        "零售业",
        "住宿业",
        "餐饮业",
        "银行业",
        "证券业",
        "保险业",
        "其他金融活动",
        "房地产业",
        "租赁业",
        "商务服务业",
        "研究与试验发展",
        "专业技术服务业",
        "科技交流和推广服务业",
        "地质勘查业",
        "水利管理业",
        "环境管理业",
        "公共设施管理业",
        "居民服务业",
        "其他服务业",
        "教育",
        "卫生",
        "社会保障业",
        "社会福利业",
        "新闻出版业",
        "广播、电视、电影和音像业",
        "文化艺术业",
        "体育",
        "娱乐业",
        "中国共产党机关",
        "国家机构",
        "人民政协和民主党派",
        "群众团体、社会团体和宗教组织",
        "基层群众自治组织",
        "国际组织",
        "楼宇"
      ],
      checkTel: (rule, value, callback) => {
        if (!value) {
          return callback(new Error("手机号不能为空"));
        } else {
          const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
          if (reg.test(value)) {
            callback();
          } else {
            return callback(new Error("请输入正确的手机号"));
          }
        }
      },
      rules: {
        cname: [
          { required: true, message: "请输入客户名称", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20个字符", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      this.axios
        .get(this.baseUrl + "client/queryClientId", {
          params: { id: this.$route.query.id }
        })
        .then(res => {
          this.form = res.data;
        });
    },
    addItem() {
      this.form.linkmanList.push({
        dept: "",
        email: "",
        flag: 0,
        hiredate: "",
        job: "",
        lname: "",
        ltel: "",
        officePhone: ""
      });
    },
    deleteItem(item, index) {
      this.form.linkmanList.splice(index, 1);
    },
    handleChange(value) {
      var addr = "";
      addr += CodeToText[value[0]];
      addr += CodeToText[value[1]];
      addr += CodeToText[value[2]];
      alert(addr);
      this.form.address = addr;
    },
    submitUser(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var data = {
            id: this.form.id,
            cname: this.form.cname,
            unitCount: this.form.unitCount,
            insureCount: this.form.insureCount,
            industryType: this.form.industryType,
            intention: this.form.intention,
            unitType: this.form.unitType,
            address: this.form.address,
            linkmanList: this.form.linkmanList
          };
          this.axios
            .put(
              `${this.baseUrl}client/updateClient`,
              this.$qs.stringify(data, {
                arrayFormat: "indices",
                allowDots: true
              })
            )
            .then(res => {
              if (res.data.code == 201) {
                this.$message({
                  message: res.data.message,
                  type: "success"
                });
                setTimeout(() => {
                  this.$router.push({ name: "localUser" });
                }, 2000);
              } else {
                this.$message({
                  message: res.data.message,
                  type: "error"
                });
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style>
.el-cascader-menu {
  min-width: 110px;
}
</style>



